import { Message } from 'element-ui'
import BScroll from 'better-scroll'
import axios from 'axios'
export default {
    data(){
        return {
            lists:[],
            page:1,
            index:0,
            next:0,
            scroll:''
        }
    },
    created(){
        let localLists = JSON.parse(window.sessionStorage.getItem('top250'))
        if (localLists) {
            this.lists = localLists
        } else {
            this.getData()
        }
        this.ready()
    },
    methods:{
        getData(){
            axios({
                method:'patch',
                url:'https://api.douban.com/v2/movie/top250',
                data:{
                    "start":0,
                    "count":40
                }
            }).then((res)=>{
                this.lists = res.data.subjects
                let localLists = JSON.stringify(this.lists)
                window.sessionStorage.setItem('top250',localLists)
                this.next +=100
            }).catch(error => {
                console.log(error)
            })
        },

        handleScroll () {
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            // let scrollHeight = Math.floor(this.$refs.itemHeight.offsetHeight) - Math.floor(scrollTop)
            // console.log(scrollTop)
            // console.log(this.$refs.itemHeight.offsetHeight)
            // if( scrollHeight <= 1000){
            //     this.getData()
            // }
        },
        ready () {
            window.addEventListener('scroll', this.handleScroll);
        },
        mounted() {
            this.$nextTick(() => {
              //$refs绑定元素
              if(!this.scroll){
                  this.scroll = new BScroll(this.$refs.wrapper, {
                  //开启点击事件 默认为false
                  click:true
              })
              // console.log(this.scroll)
              }else if(!this.$refs.wrapper){
                  return
              }
              else{
                //   this.scroll.refresh()
              }
            })
        }        
    }
}


